import React from 'react';
import { Pie } from 'react-chartjs-2';

const PieChart = ({ chartData }) => {
  return (
    <div className="chart-container">
      <Pie
        data={chartData}
        options={{
          plugins: {
            title: {
              display: true,
              text: 'Monthly Payment Charges',
            },
          },
        }}
      />
    </div>
  );
};

export default PieChart;

